<template>
  <div id="Vbee-login">
    <!---->
    <div id="user-form">
      <div class="target-title">
        <p id="targetCon"></p>
      </div>
      <!---->
      <div class="user-name">
        <input type="text" id="username" placeholder=" /  / ">
      </div>
      <!---->
      <div class="user-pass">
        <!---->
        <img id="demo_img" src="../../../static/images/eyeclose.png" @click="hideShowPsw()">
        <input type="password" id="demo_input" placeholder=""/>
      </div>
      <!---->
      <div class="forget-pass">
        <a href="#"></a>
      </div>
      <!---->
      <div class="user-btn">
        <input type="submit" value="  " @click="hasinfo">
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "WebzLog",
      methods:{
        hideShowPsw:function () {
          // jsjquery
          var demoImg = document.getElementById("demo_img")
          var demoInput = document.getElementById("demo_input")

          //text blockpassword block
          if (demoInput.type == "password") {
            demoInput.type = "text";
            demoImg.src = "./static/images/eyeopen.png";
          }else {
            demoInput.type = "password";
            demoImg.src = "./static/images/eyeclose.png";
          }
        },

        // input
        hasinfo:function(){
          var demoName = document.getElementById("username")
          var demoInput = document.getElementById("demo_input")
          var demoText = document.getElementById("targetCon")
          console.log(demoName.value)
          console.log(demoInput.value)
          if(demoName.value =="" || demoInput.value == ""){
            demoText.style.display = "block"
            demoText.innerHTML ="";
          }else if(demoName.onblur || demoInput.onblur){
            demoText.style.display = "none"
            demoText.innerHTML = ""
          }else{
            demoText.style.display = "none"
            demoText.innerHTML = ""
          }

        },
        //input 

      }
    }
</script>

<style scoped>
  #Vbee-login{
    padding:0 30px;
  }
  #user-header img{
    width: 80px;
    height:80px;
    display: block;
    margin:0 auto;
    border-radius:50%;
  }
  #user-form{
    padding:30px;
  }
  /**/
  #user-form .target-title p{
    font-size: .1rem;
    color:#e32b1f;
    display: none;
  }
  /**/
  #user-form .user-name{
    border-bottom: 2px solid #d3d3d8;
  }
  /**/
  #user-form .user-pass{
    border-bottom: 2px solid #d3d3d8;
    position: relative;
  }
  /**/
  #demo_img{
    width: 20px;
    height: 15px;
    display: block;
    position: absolute;
    right: 24px;
    top:20px;
  }
  /*forminput*/
  #user-form input{
    width: 100%;
    height: 50px;
    font-size: 14px;
    color:#c3c2c3;
    border:none;
    outline: none;
    line-height:50px;
    display: flex;
    text-indent: 10px;
  }
  /**/
  .forget-pass{
    display: flex;
    justify-content:flex-end;
    height: 40px;
    align-items: center;
    padding-bottom: 10px;
  }
  .forget-pass a{
    font-size: .1rem;
    color:#87888d;
  }
  /*.*/
  .user-btn input{
    height: .4rem;
    background: #16c9e9;
    color:#f4add7;
    font-size: .12rem;
    display: flex;
    justify-content: center;
  }
</style>



