<template>
  <div id="dateContainer">
    <div class="date__showNowTime">
       <span v-text="ynow"></span><span v-text="mnow+1"></span><span v-text="dnow"></span>
    </div>
    <div class="nowTime">
      <button class="preMon" @click="preMon"></button>
      <div class="thisMon">
        <span v-text="ynow"></span>/
        <span v-text="mnow+1"></span>
      </div>
      <button class="nextMon" @click="nextMon"></button>
    </div>
    <table border="1" id="table">

    </table>

  </div>
</template>

<script>
    export default {
        name: "WebzNews",
      data() {
        return {
          newDate: '',//
          ynow: '',//
          mnow: '',//
          dnow: '',//
          firstDay: '',//
          firstnow: '',//
          m_days: [],
          tr_str: '',
        }
      },
      methods: {
        getDaysInfo() {
          var _this = this;
          this.sureDate(_this);
        },
        is_leap(year) {
          return (year%100==0?(year%400==0?1:0):(year%4==0?1:0));
        },
        //
        drawTable(jsonHtml) {
          var _this = this;
          var str = `
        <tr class="xiqi">
          <td class="isRed"></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td class="isRed"></td>
        </tr>`;
          var idx = '',date_str = '',isRed = '',hasMsg='';
          for(var i = 0; i< _this.tr_str; i++) {
            str+='<tr>';
            for(var k = 0; k < 7; k++) {
              idx = i*7+k;
              isRed = (k===0||k===6)?"isRed":"";
              date_str=idx-_this.firstnow+1;
              (date_str<=0 || date_str>this.m_days[this.mnow]) ? date_str="&nbsp;" : date_str=idx-_this.firstnow+1;
              date_str==_this.dnow?hasMsg='<td class="thisDay" date="'+date_str +'"><span  class="'+isRed +'">'+date_str+'</span></td>':hasMsg='<td date="'+date_str +'"><span  class="'+isRed +'">'+date_str+'</span></td>';
              for(var l = 0, len = jsonHtml.length; l < len; l++) {
                if(date_str== jsonHtml[l].date) {
                  var newStr = '<p>'+jsonHtml[l].msg +'</p>';
                  date_str==_this.dnow?hasMsg='<td class="thisDay" date="'+date_str +'"><span  class="'+isRed +'">'+date_str+'</span>'+ newStr+'</td>':hasMsg='<td date="'+date_str +'"><span  class="'+isRed +'">'+date_str+'</span>'+ newStr+'</td>';
                }
              }
              str+=hasMsg;
            }
            str+='</tr>';
          }
          var table = document.getElementById('table');
          table.innerHTML = str;
        },
        //this
        sureDate(_this,other) {
          this.newDate = new Date();
          this.ynow = this.newDate.getFullYear();
          if(!other) {
            this.mnow=this.newDate.getMonth(); //
          }
          this.dnow=this.newDate.getDate(); //
          this.firstDay = new Date(this.ynow,this.mnow,1);
          this.firstnow=this.firstDay.getDay();
          this.m_days = [31,28+this.is_leap(this.ynow),31,30,31,30,31,31,30,31,30,31];
          this.tr_str = Math.ceil((_this.m_days[this.mnow] + this.firstnow)/7);
          this.showMsg();
        },
        preMon() {
          var _this = this;
          this.mnow--;
          this.sureDate(_this,"up");
        },
        nextMon() {
          var _this = this;
          this.mnow++;
          this.sureDate(_this,"next");
        },
        //
        showMsg() {
          var jsonHtml = [{
            date: 2,
            msg: ''
          },
            {
              date: 3,
              msg: ''
            },
            {
              date: 4,
              msg: ''
            },
            {
              date: 6,
              msg: ''
            },
            {
              date: 12,
              msg: '~~~~~'
            },
            {
              date: 15,
              msg: '~~~~'
            },
            {
              date: 20,
              msg: ''
            }];
          this.drawTable(jsonHtml)
        }
      },
      mounted() {
        //
        this.getDaysInfo();
        //
      },
      watch: {
        mnow: function(newVal,oldVal) {
          if(newVal!== oldVal) {
          }
        }
      }
    }
</script>

<style lang="scss" scoped>
  #dateContainer {
    width: 100%;
    background-color: #1ebfaf;
    border: 1px solid #2fa5ff;
    min-width: 400px;
    box-sizing: border-box;
    margin-bottom: 10px;
    .nowTime {
      display: flex;
      justify-content: center;
      height: 30px;
      line-height: 30px;
      .thisMon {
        height: 30px;
        line-height: 30px;
        span {
        }
      }
    }
    table {
      width: 100%;
      border: 2px solid orange;
      tr {
        width: 100%;
        border: 1px solid yellowgreen;
        border-right: none;
        border-left: none;
        display: flex;
        justify-content: space-around;
        td {
          flex: 1;
          border-right: 2px solid yellowgreen;
          text-align: center;
          height: 100px;
          span[date] {
            position: relative;
            z-index: 999;
          }
        }
        tr:last-child {
          border: none;
        }
        td.thisDay {
          background-color: #eee;
        }
        .isRed {
          color: red;
        }
      }
      tr.xiqi {
        td {
          height: 22px;
          line-height: 22px;
        }
      }
    }
  }
</style>



