<template>
 <div class="login">
   <div class="login-1">
      <p></p>
     <div>
       &lt;template&gt;<br/>
       &lt;div id="Vbee-login"&gt;<br/>
       &lt;!----&gt;<br/>
       &lt;div id="user-form"&gt;<br/>
       &lt;div class="target-title"&gt;<br/>
       &lt;p id="targetCon"&gt;&lt;/p&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;!----&gt;<br/>
       &lt;div class="user-name"&gt;<br/>
       &lt;input type="text" id="username" placeholder=" /  / "&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;!----&gt;<br/>
       &lt;div class="user-pass"&gt;<br/>
       &lt;!----&gt;<br/>
       &lt;img id="demo_img" src="../../../static/images/eyeclose.png" @click="hideShowPsw()"&gt;<br/>
       &lt;input type="password" id="demo_input" placeholder=""/&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;!----&gt;<br/>
       &lt;div class="forget-pass"&gt;<br/>
       &lt;a href="#"&gt;&lt;/a&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;!----&gt;<br/>
       &lt;div class="user-btn"&gt;<br/>
       &lt;input type="submit" value="  " @click="hasinfo"&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;/template&gt;<br/>

     </div>
   </div>
   <div class="login-2">
     <webz-log></webz-log>
   </div>
 </div>
</template>

<script>
    import WebzLog from "../webZuja/WebzLog";
    export default {
        name: "WebRightLog",
      components: {WebzLog},
    }
</script>

<style lang="scss" scoped>
  .login{
    width:100%;
    height:100%;
    overflow: hidden;
    .login-1{
      width:50%;
      height:100%;
      float: left;
      padding-left: 15px;
      padding-top: 35px;
      background-color: rgba(135,206,235,.8);
      box-sizing: border-box;
      overflow-x: hidden;
      overflow-y: auto;
      &::-webkit-scrollbar{
        width: 0;
      }
      p{
        font-size: 16px;
        font-weight: 600;
      }
      div{
        font-size: 14px;
        padding: 10px 15px;
      }
    }
    .login-2{
      width: 50%;
      height:100%;
      float: left;
    }
  }
</style>



